// Zadanie 1
.one-image {
  width: 500px;
  height: 400px;
  border: 1px solid red;
  img {
    width: 100%;
    height: 100%;
  }
}


// Zadanie 2
.one-image-bg {
  width: 500px;
  height: 400px;
  border: 1px solid red;
  .rabbit {
    background-image:url('../images/rabbit.png');
    background-size: 100% 100%;
    background-repeat: no-repeat;
    width: 100%;
    height: 50%;
  }
}
